.inputWrapper {
    display: flex;
    flex-direction: column;
    position: relative;
}


.timePicker {
    position: absolute;
    bottom: -18px;
    left: 0;
    transform: translateY(100%);
    z-index: 25;
    display: flex;
    align-items: center;
    width: 100%;
    height: 160px;
    padding: 0px 16px;
    /* flex-direction: column;
  align-items: flex-start; */
    gap: 8px;

    border-radius: 8px;
    background: #fff;

    /* sdw2 */
    box-shadow: 2px 4px 9px 0px rgba(166, 141, 174, 0.28);

    color: #aca7c3;
    text-align: center;
    font-family: Poppins;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;

    overflow: hidden;
}

.visionLine {
    position: relative;
}

.visionLine::after {
    content: '';
    position: absolute;
    top: -26px;
    left: 0;
    width: 337px;
    height: 48px;
    border-top: 1px solid yellow;
    border-bottom: 1px solid lightblue;
}

.visionLine::before {
    content: ':';
    position: absolute;
    color: #3f3f3f;
    top: -13px;
    left: 83px;
}

.swiper {
    width: 73px;
    height: 48px;
}

.swiperSlide {
    display: flex;
    width: 73px;
    padding: 12px 16px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 4px;
    flex-shrink: 0;
    cursor: pointer;
}

@media screen and (max-width: 768px) {
    .swiperSlide {
        width: 40px;
    }
}

.selected {
    color: #3f3f3f;
}